.filter-table {
  background: #fff;
  padding: 20px;
  text-align: right;
}
.ant-modal-body .ant-form-item-label > label {
  min-width: 80px;
}
.config-tip {
  float: right;
  width: 30px;
  height: 30px;
  margin-top: 7px;
  background-repeat: no-repeat;
  background-position: center;
  background-size: 20px auto;
}
.config-tishi {
  background-image: url('../images/tishi.png');
}
.config-tishi-active {
  background-image: url('../images/tishi-active.png');
}
.center {
  text-align: center;
}
html,
body,
div,
p,
ul,
ol,
li {
  padding: 0;
  margin: 0;
}
i,
em {
  font-style: normal;
}
ul,
li,
ol {
  list-style: none;
}
a {
  text-decoration: none;
  display: block;
  color: #4a4a4a;
}
a:hover {
  color: #444;
}
html,
body {
  height: 100%;
  color: #4a4a4a;
  background: #f9f9f9;
  font-family: Helvetica, Arial, sans-serif;
  font-size: 14px;
}
#root,
.ant-layout,
.App {
  height: 100%;
  width: 100%;
}
::-webkit-scrollbar {
  width: 4px;
  height: 10px;
}
::-webkit-scrollbar-track {
  /* -webkit-box-shadow:inset006pxrgba(0,0,0,0.3); */
  /* border-radius:6px; */
}
::-webkit-scrollbar-thumb {
  /* border-radius:6px; */
  background: rgba(0, 0, 0, 0.2);
  /* -webkit-box-shadow:inset006pxrgba(0,0,0,0.5); */
}
::-webkit-scrollbar-thumb:window-inactive {
  /* background:rgba(255,0,0,0.4); */
}
.submit-btn {
  background: #FA5501;
  color: #fff;
}
.submit-btn:hover {
  background: #FA5501;
}
.reset-btn {
  background: #444;
  color: #fff;
}
.ant-table.ant-table-bordered > .ant-table-container > .ant-table-header > table > thead > tr > th {
  text-align: center;
}
.ant-modal-footer {
  text-align: center;
}
.ant-input-number {
  width: 100%;
}
.table-panel-wrapper {
  overflow: auto;
  color: #666;
  flex: 1;
  position: relative;
  background: #fff;
  padding: 20px 20px 60px;
  overflow: hidden;
}
.table-panel-wrapper .ant-table-container,
.table-panel-wrapper .ant-table-wrapper,
.table-panel-wrapper .ant-table,
.table-panel-wrapper .ant-spin-nested-loading,
.table-panel-wrapper .ant-spin-container {
  height: 100%;
}
.table-panel-wrapper .ant-table-thead {
  white-space: nowrap;
}
.table-panel-wrapper .pagination-wrapper {
  position: absolute;
  right: 20px;
  bottom: 20px;
}
.table-panel-wrapper .ant-select,
.table-panel-wrapper .ant-picker,
.table-panel-wrapper .ant-input-number {
  width: 100%;
}
.table-panel-wrapper .table-btn {
  margin: 4px;
}
.table-panel-wrapper .table-red {
  width: 100%;
  height: 100%;
  background: #eb3407;
  color: #fff;
}
.react-resizable {
  position: relative;
  background-clip: padding-box;
}
.react-resizable-handle {
  position: absolute;
  width: 10px;
  height: 100%;
  bottom: 0;
  right: -5px;
  cursor: col-resize;
  z-index: 1;
}
.ant-menu-horizontal {
  width: 600px;
}
.ant-form-item {
  padding: 0 10px;
}
.page-header {
  padding: 0 30px;
  background: #fff;
  display: flex;
  justify-content: space-between;
}
.page-container {
  height: calc(100% - 46px);
  background: #f1f1f1;
  padding: 20px 30px;
}
.page-form {
  background: #fff;
  border-radius: 8px;
  padding: 20px 30px;
  margin-bottom: 15px;
}
.btn {
  margin-left: 10px;
}
.btn:hover {
  background: transparent;
}
.bg-red {
  background: #f52e2e;
}
.bg-yellow {
  background: #e5f051;
}
